<template>
    <!-- 首页二手房 -->
    <div class="erf_index">
        <div class="package_a">
           <ul style="counter-reset: step 0;"> 
            <li class="package_aitem"  v-for="(item,index) in alist" :key='index'>
              <!-- 未售 -->  
              <div class="noPay" @click="toPay(item, 'A' + index, 0)" v-show="!item[item.length - 1].is_sale">
                <div>
                   <i class="iconfont">&#xe600;</i>
                </div>

                <div class="noPay_hover">
                  <span>点击购买</span>
                </div>
               
              </div>
              
              <!-- 已售 -->
              <div class="hasPay" v-show="item[item.length - 1].is_sale"> 
                <div class="endTime">
                  <p>到期时间</p>
                  <p>{{item[item.length - 1].endtime | formatDate}}</p>

                  <div class="addHouse" @click="showHouses(item[item.length - 1], 'A' + index)" v-show="showBtn(item[item.length - 1].agent_id)">
                      <i class="el-icon-circle-plus-outline"></i>
                      <p>添加房源</p>
                  </div>
                </div> 
                <span class="renewal" @click="toPay(item, 'A' + index, 1)" v-show="showBtn(item[item.length - 1].agent_id)">续费</span>
              </div>
            </li> 
          </ul>
        </div>
        <div class="package_b"> 
             <ul style="counter-reset: step 0;">
                <li class="package_bitem" v-for="(item,index) in blist" :key='index'> 

                    <!-- 已售 -->
                    <div class="hasPay" v-show="item[item.length - 1].is_sale">
                        <span>到期时间{{item[item.length - 1].endtime | formatDate}}</span>
                        <span  @click="toPay(item, 'B' + index, 1)" v-show="showBtn(item[item.length - 1].agent_id)" class="bitem_renewal">续费</span>
                        

                        <div class="hasPay_hover" v-show="showBtn(item[item.length - 1].agent_id)" @click="showHouses(item[item.length - 1], 'B' + index)">
                            <i class="el-icon-circle-plus-outline"></i>
                            <span>添加房源</span>
                        </div>
                    </div> 

                    <!-- 未售 -->
                  <div class="noPay" @click="toPay(item, 'B' + index, 0)" v-show="!item[item.length - 1].is_sale">
                      <div class="hover_item_wrap" v-show="!item[item.length - 1].is_sale">点击购买</div>
                      <i class="iconfont">&#xe600;</i>
                  </div>  
                  
                </li>
            </ul>
        </div>

        
        <!-- addHouse dialog-->
        <el-dialog
            :title="nowTitle + '套餐'"
            border
            stripe
            :visible.sync="addhouseDialogVisible" 
            width="50%"> 
            <div class="topSearch fr">
              <el-input style="width: 300px;" placeholder="请输入标题/小区名称/房源编号" v-model="keywords" class="input-with-select">
                <el-button slot="append" icon="el-icon-search" @click="getData"></el-button>
              </el-input>
            </div>

             <el-table
                ref="singleTable"
                :data="tableData"
                highlight-current-row
                @current-change="handleCurrentChange"
                style="width: 100%"> 
                <el-table-column width="50">  
                  <template slot-scope="scope"> 
                    <i v-show="checkedList == scope.row.id" class="iconfont" style="color: #409EFF;font-size: 20px;">&#xe646;</i>
                    <i  v-show="checkedList != scope.row.id" class="iconfont" style="color: #dcdfe6;font-size: 20px;">&#xe604;</i>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="sale_no"
                  label="编号"
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="title"
                  label="标题">
                </el-table-column> 
            </el-table>
            <div class="text-right" style="margin-top: 30px;margin-bottom: 30px;"> 
              <el-pagination 
                background
                layout="total, sizes, prev, pager, next"
                :page-sizes="[10, 20, 30, 50]"
                @current-change='currentChange'
                :current-page="page.pageNo"
                :page-size="page.pageSize"
                :total="page.total"
                >
              </el-pagination>
             </div>
            <div class="text-center">
              <el-button type="primary" round @click="addHouse">确认添加</el-button>
            </div>
            
        </el-dialog>

        <!-- pay dialog -->
        <el-dialog
            title="购买套餐"
            :visible.sync="dialogVisible" 
            width="30%"> 
          
          <p class="dialog_title">{{nowTitle}}套餐</p>
          <div>
            <p class="dialog_tip"><i>*</i>请选择套餐类型</p>
            <el-radio-group v-model="selected"> 
              <el-radio @change='selectedChange(item)' v-show="index != (nowData.length - 1)" v-for="(item,index) in nowData" :key="item.id" :label="item.id">{{item.money + '元 / '}}{{item.cycle_time + '天'}}</el-radio>
            </el-radio-group>

            <div class="choose">
              <p class="dialog_tip"><i>*</i>请选择支付类型</p>
              <span class="choose_pay" style="margin-right: 40px;" @click="showCode(0)"> 
                <img src="@/assets/image/alipay.png" alt="支付宝" width="117" height="41">
              </span>
              <span class="choose_pay" @click="showCode(1)"> 
                <img src="@/assets/image/wxpay.png" alt="微信">
              </span>
            </div>
          </div>

           <el-dialog
            width="30%"
            title="待付款"
            :visible.sync="innerVisible"
            @open='showinner'
            append-to-body>
              <div class="code" id="qrcode"></div>
              <p class="text-center">扫码支付</p>
              <p class="text-center" style="margin-top: 30px;">请用微信扫描上方二维码完成支付<span style="color: #FF6E5C">（30分钟之内请尽快扫码支付)</span></p>
          </el-dialog>

        </el-dialog>

        

    </div>
</template>

<script>
import { getId } from "@/utils/auth";
import { getSaleRentShopList } from "@/api/house";
import { increment, aliOrder, wechatOrder, overPay, UpOrderBuy } from "@/api/meal";
import QRcode from "@/utils/qrcode.js";
export default {
  components: {},
  data() {
    return {
      nowTitle: "",
      page: {
        pageNo: 1,
        pageSize: 10,
        total: 0
      },
      is_renew: '',
      addhouseDialogVisible: false,
      keywords: "",
      checkedList: "",
      tableData: [],
      dialogVisible: false,
      innerVisible: false,
      alist: [],
      blist: [],
      nowData: [],
      selected: "",
      selectedForm: {},
      timer: null,
      qrcode: "",
      order_no: ""
    };
  },
  created() {
    this.fetchdata();
  },
  watch: {
    dialogVisible (val) {
      if (!val) {
        this.order_no = ""
        this.fetchdata()
      }
    },
    innerVisible (val) {
       if (!val) {
        this.order_no = ""
        clearInterval(this.timer)

        this.fetchdata()
      }
    },
    addhouseDialogVisible (val){
      if (!val) {
        this.order_no = ""
        this.checkedList = ""
      }
    }
  },
  methods: {
    getData() {
      let data = {
        pageNo: this.page.pageNo,
        pageSize: this.page.pageSize,
        select_type: 1, //1:二手房，2租房，3商铺
        updown: 1,
        keywords: this.keywords
      };
      getSaleRentShopList(data).then(res => {
        this.tableData = res.data.data.data;
        this.page.total = res.data.data.total;
      });
    },
    handleCurrentChange(val) {
      if (val) {
           this.checkedList = val.id; 
      }
       
    },
    // 检测页数变化
    currentChange(page) {
      this.page.pageNo = page;
      this.getData();
    },
    /* 房源添加显示 */
    showHouses(item, title) { 
      this.order_no = item.order_no
      this.nowTitle = title;
      this.getData()
      this.addhouseDialogVisible = true;
    },
    /* 添加 */
    addHouse() { 
      if (!this.checkedList) {
        this.$message.warning('请选择')
        return
      }
      UpOrderBuy({id: this.checkedList,order_no: this.order_no,}).then(res => {
        if (res.data.code == 1) {
          this.addhouseDialogVisible = false
        }
      })  
    },
    showBtn(val) {
      if (val == getId()) {
        return true;
      } else {
        return false;
      }
    },
    fetchdata() {
      let data = {
        location_type: 1, // 表示类型
        meal_type: 1,
        type: 1
      };
      increment(data).then(res => {
        if (res.data.code == 1) {
          this.alist = res.data.data.A;
          this.blist = res.data.data.B;
        }
      });
    },
    selectedChange(val) {
      this.selectedForm = Object.assign({}, val);
    },
    toPay(item, title, is_renew) {
      this.nowTitle = title;
      this.nowData = item;
      this.is_renew = is_renew
      this.dialogVisible = true;
    },
    showCode(type) {
      if (!this.selected) {
        this.$message.warning("请选择套餐类型");
        return;
      }
      /* 支付宝 */
      let data = {
        meal_type: 1,
        menu_id: this.selectedForm.id,
        agent_id: getId(),
        meal_level: "首页二手房",
        cycle_time: this.selectedForm.cycle_time,
        pay_money: this.selectedForm.money,
        location_type: 1,
        location_id: this.selectedForm.location_id,
        is_renew: this.is_renew
      };
      if (type == 0) {
        aliOrder(data).then(res => {
          if (res.data.code == 1) {
            this.qrcode = res.data.data[0]; 
            this.order_no = res.data.data[1];

            this.innerVisible = true;
          }
        });
      } else if (type == 1) {
        /* 微信 */
        wechatOrder(data).then(res => {
          if (res.data.code == 1) {
            this.qrcode = res.data.data[0]; 
            this.order_no = res.data.data[1];
            this.innerVisible = true;
          }
        });
      }
    },
    showinner() {
      this.$nextTick(() => {
        document.getElementById("qrcode").innerHTML = "";
        var qrcode = new QRCode(document.getElementById("qrcode"), {
          width: 150,
          height: 150
        });
        qrcode.clear();
        qrcode.makeCode(this.qrcode);

        /* 定时器，接口 */ 
        var self = this;
        this.timer = setInterval(verifyHandle, 3000);
        function verifyHandle() {
          overPay({order_no: self.order_no}).then((res) => {
            if (res.data.code == 1) {
              self.$message.success('支付成功')
              self.fetchdata()
              clearInterval(self.timer)
              self.dialogVisible = false
              self.innerVisible = false
            }
          });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.erf_index {
  display: flex;
  margin-right: 20px;
}
.package_a {
  flex: 1;
  .package_aitem {
    position: relative;
    display: inline-block;
    margin-right: 20px;
    width: 30%;
    height: 180px;
    margin-bottom: 20px;

    .noPay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      background: #c9d3df;
      cursor: pointer;
      i {
        display: inline-block;
        margin-top: 50px;
        font-size: 60px;
        color: #fff;
      }
    }
    .noPay_hover {
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      color: #fff;
      font-size: 16px;
      padding-top: 70px;
      z-index: 4;
      background: rgba(0, 0, 0, 0.3);
    }
    .hasPay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      color: #fff;
      background: #028ca2;
      opacity: 0.4;
      .addHouse {
        i {
          font-size: 46px;
          padding-top: 8px;
          padding-bottom: 10px;
          cursor: pointer;
        }
      }
      .endTime {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
      }
      .renewal {
        position: absolute;
        top: 10px;
        right: 10px;
        border: 1px solid #fff;
        border-radius: 20px;
        padding: 2px 10px;
        cursor: pointer;
      }
    }
    &::before {
      position: absolute;
      left: 2px;
      top: 4px;
      counter-increment: step;
      content: "A" counter(step);
      display: inline-block;
      vertical-align: middle;
      text-align: center;
      color: #fff;
      z-index: 3;
      font-size: 12px;
    }
    &::after {
      position: absolute;
      content: "";
      top: -15px;
      left: -3px;
      border: 24px solid #3399ff;
      border-top-color: transparent;
      border-bottom-color: transparent;
      border-left-width: 0;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
    }
    &:hover .noPay_hover {
      opacity: 1;
      transition: all 0.4s;
    }
  }
}
.package_b {
  width: 300px;
  .package_bitem {
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #fff;
    margin-bottom: 20px;
    cursor: pointer;
    .hover_item_wrap {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      text-align: center;
      background: rgba(0, 0, 0, 0.3);
    }
    .noPay {
      height: 40px;
      line-height: 40px;
      cursor: pointer;
      background: #c9d3df;
      text-align: center;
      color: #fff;
      i {
        font-size: 20px;
      }
    }
    &:before {
      position: absolute;
      counter-increment: step;
      content: "B" counter(step);
      display: inline-block;
      vertical-align: middle;
      width: 40px;
      height: 40px;
      line-height: 40px;
      background: #00a06b;
      text-align: center;
      color: #fff;
    }
    &:hover .hover_item_wrap {
      display: block;
    }

    .hasPay {
      height: 40px;
      line-height: 40px;
      cursor: pointer;
      background: #c9d3df;
      text-align: center;
      color: #fff;
      .bitem_renewal {
        position: absolute;
        right: 10px;
        color: red;
        z-index: 5;
      }
      .hasPay_hover {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        text-align: center;
        background: rgba(0, 0, 0, 0.5);
      }
      i {
        font-size: 20px;
      }

      &:hover .hasPay_hover {
        display: block;
      }
    }
  }
}

.dialog_title {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: rgba(44, 62, 80, 1);
  margin-top: -20px;
  margin-bottom: 20px;
}
.choose {
  margin-top: 40px;
  margin-bottom: 40px;
}
.choose_pay {
  display: inline-block;
  width: 180px;
  height: 60px;
  text-align: center;
  border: 1px solid rgba(240, 242, 245, 1);
  border-radius: 5px;

  cursor: pointer;
  img {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }

  &:hover {
    border-color: #3399ff;
  }
}

.dialog_tip {
  margin-bottom: 10px;
  i {
    color: red;
    margin-right: 10px;
  }
}
.code {
  position: relative;
  left: 32%;
  width: 204px;
  height: 160px;
}
</style>



