<template>
     <div class="public_content">
       <div class="public_title"><span>套餐管理—展示型套餐管理</span></div>
       <div class="show_meal"> 
     
          <div class="nav_wrap"> 
              <div class="tab_btn"
                  v-for="(tab,index) in tagbtn" 
                  :key='index' 
                  @click="currentTab = tab.name"
                  :class="[{ active: currentTab === tab.name }]"
              >
                  {{tab.label}}
              </div>
          </div>
           
          <div class="content"> 
              <transition name="list" mode="out-in">
                  <keep-alive>
                      <component :is="currentTabComponent"></component>
                  </keep-alive>
              </transition>
          </div>
        </div>
    </div>
</template>

<script>

/* 需要做判断是店长还是个人 */
import {
  aggent,
  esfIndex,
  esfList,
  partner,
  rentIndex,
  rentList,
  aggentList,
  shopList,
  storeIndex,
  storeList
} from "./components";

export default {
  data() {
    return {
      tagbtn: [
        {
          name: "esfIndex",
          label: "首页二手房"
        },
        {
          name: "rentIndex",
          label: "首页租房"
        },
        {
          name: "storeIndex",
          label: "首页商铺"
        },
        {
          name: "aggent",
          label: "首页经纪人"
        }, 
        // {
        //   name: "partner",
        //   label: "战略合作伙伴"
        // }, 
        {
          name: "esfList",
          label: "列表二手房"
        },
        {
          name: "rentList",
          label: "列表租房"
        },
        {
          name: "storeList",
          label: "列表商铺"
        },
        {
          name: "aggentList",
          label: "列表经纪人"
        },
        // {
        //   name: "shopList",
        //   label: "列表经纪人"
        // }
      ],
      currentTab: "esfIndex"
    };
  },
  computed: {
    currentTabComponent: function() {
      return this.currentTab.toLowerCase();
    }
  },
  components: {
    aggent,
    esfindex: esfIndex,
    esflist: esfList,
    partner,
    rentindex: rentIndex,
    rentlist: rentList,
    aggentlist: aggentList,
    storeindex: storeIndex,
    storelist: storeList
  }
};
</script>

<style lang="scss" scoped>
.show_meal {
  display: flex;
}
.nav_wrap { 
  width: 200px;
  .tab_btn {
    font-size: 16px;
    cursor: pointer;
    margin: 20px 0;
    padding-left: 20px;
    border-left: 2px solid transparent;
    &.active {
      color: #36a3f7;
      border-left-color: #36a3f7;
    }
  }
}
.content {
  flex: 1;
  margin-top: 18px;
}

.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-enter-active,
.list-leave-active {
  transition: all 0.4s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
}
</style>



