import request from "@/utils/request";

export function increment(params) {
  return request({
    url: "/Increment/increment",
    method: "get",
    params
  });
}

export function aliOrder(data) {
  return request({
    url: "/alipay/aliOrder",
    method: "post",
    data
  });
}

export function wechatOrder(data) {
  return request({
    url: "/wechatpay/wechatOrder",
    method: "post",
    data
  });
}

/* 验证 */
export function overPay(data) {
  return request({
    url: "/receive/overPay",
    method: "post",
    data
  });
}

/* 添加 */
export function UpOrderBuy(params) {
  return request({
    url: "/Increment/UpOrderBuy",
    method: "get",
    params
  });
}








